import React, { Component } from 'react';

export default class Console extends Component {
	static defaultProps = {
		title: 'david@internet:~',
		lines: []
	};
	constructor(props) {
		super(props);
		this.state = {
			lines: [ '' ],
			doneWriting: false
		};
	}

	componentDidMount() {
		this.writeAllLines();
		document.addEventListener('keydown', this.handleKeypress);
	}

	componentWillUnmount() {
		clearInterval(this.intervalId);
	}

	writeAllLines = async () => {
		let i = 0;
		for (let line of this.props.lines) {
			await this.writeLine(line, i++);
		}
		this.setState({ doneWriting: true });
	};

	writeLine = (text, id, delay = 100) => {
		return new Promise((resolve, reject) => {
			this.intervalId = setInterval(() => {
				const currentText = this.state.lines[id] || '';
				let line = text.slice(0, currentText.length + 1);
				if (line.endsWith(' ')) line = text.slice(0, currentText.length + 2);
				const lines = this.state.lines.map((text, i) => {
					if (i === id) {
						return line;
					} else {
						return text;
					}
				});

				if (text.length === line.length) {
					clearTimeout(this.intervalId);
					lines.push('');
					this.handleEndLine(line, id);
					resolve();
				}

				this.setState({ lines });
			}, delay);
		});
	};

	renderLines = () => {
		return this.state.lines.map((text, i) => (
			<div key={i} className="Console__line">
				{text}
			</div>
		));
	};

	handleEndLine = (line, id) => {
		if (this.props.onEndLine) this.props.onEndLine(line, id);
	};

	handleKeypress = (e) => {
		if (this.state.doneWriting) {
			const { lines } = this.state;
			const newLines = [ ...lines ];

			if (e.key === 'Enter') {
				newLines.push('');
			} else if (e.key === 'Backspace') {
				newLines[lines.length - 1] = newLines[lines.length - 1].slice(0, -1);
			} else if (!e.key.match(/Shift|Control|Alt/)) {
				newLines[lines.length - 1] += e.key;
			}

			this.setState((st) => {
				return { lines: newLines };
			});
		}
	};

	render() {
		return (
			<div className="Console">
				<div className="Console__header">
					<div className="Console__butons">
						<div className="Console__btn close" />
						<div className="Console__btn minimize" />
						<div className="Console__btn maximize" />
					</div>
					<div className="Console__headerText">{this.props.title}</div>
				</div>
				<div className="Console__content">{this.renderLines()}</div>
			</div>
		);
	}
}
