import React, { Component } from 'react';
import Transition from '../components/Transition';
import { AppContext } from '../contexts/AppContext';

export default class Home extends Component {
	static contextType = AppContext;
	static defaultProps = {
		activities: [
			'Code',
			'React',
			'Redux',
			'Javascript',
			'Read a good book',
			'Learn someting new',
			'Colaborate',
			'Express JS'
		],
		title: 'Hi! My Name Is David Ascencio',
		subtitle: 'I Love'
	};

	constructor(props, context) {
		super(props, context);
		this.state = {
			activity: this.props.activities[0],
			title: this.renderLetters(this.props.title),
			subtitle: this.renderLetters(this.props.subtitle)
		};

		this.context.setMinMaxScroll(0, 100);
		this.context.resetScroll();
	}

	componentDidMount() {
		this.changeActivity()();
		this.animateTitle();
	}

	componentWillUnmount() {
		clearInterval(this.intervalId);
	}

	changeActivity() {
		const { activities } = this.props;
		let counter = 0;

		return () => {
			this.intervalId = setInterval(() => {
				this.setState(
					(st) => ({
						activity: activities[counter % activities.length]
					}),
					() => counter++
				);
			}, 1500);
		};
	}

	animateLetter(letter) {
		letter.classList.add('is-active');
		setTimeout(() => letter.classList.remove('is-active'), 1000);
	}

	animateTitle() {
		const letters = document.querySelectorAll('.Home__title .Home__letter');
		letters.forEach((l, i) => setTimeout(() => this.animateLetter(l), 80 * i));
	}

	renderLetters(text) {
		return text.split('').map((l, i) => (
			<div
				key={i}
				className="Home__letter"
				onPointerEnter={(e) => this.animateLetter(e.target)}
			>
				{l}
			</div>
		));
	}

	render() {
		const { activity, title, subtitle } = this.state;
		const { scroll, scrollAll, domain } = this.context;

		return (
			<React.Fragment>
				<div className="Home" style={{ transform: `translateX(-${scroll}%)` }}>
					<div className="Home__container">
						<div className="Home__title">{title}</div>
						<div className="Home__subtitle">
							{subtitle}
							<div className="Home__activitie">{activity}</div>
						</div>
					</div>
					<div className="Home__arrow" onClick={() => scrollAll()}>
						<i className="fas fa-arrow-right" />
					</div>
				</div>
				<Transition to={`${domain}/about`} text="About Me" />
			</React.Fragment>
		);
	}
}
