import React, { createContext, Component } from 'react';

export const AppContext = createContext();

export class AppProvider extends Component {
	constructor(props) {
		super(props);
		this.state = {
			delta: 20,
			scroll: 0,
			lastScroll: 0,
			retainCeroScroll: false,
			min: 0,
			max: 100,
			onScrollCb: [],
			domain: '/portfolio'
			// domain: ''
		};

		this.handleScroll = this.handleScroll.bind(this);
		this.scrollAll = this.scrollAll.bind(this);
		this.scrollUp = this.scrollUp.bind(this);
		this.scrollDown = this.scrollDown.bind(this);
		this.resetScroll = this.resetScroll.bind(this);

		document.addEventListener('wheel', this.handleScroll);
	}

	scrollUp() {
		this.setScroll(this.state.delta * -1);
	}

	scrollDown() {
		this.setScroll(this.state.delta);
	}

	scrollAll(scroll = this.state.max) {
		this.setScroll(scroll);
	}

	resetScroll() {
		this.setScroll(0, true);
	}

	setMinMaxScroll = (min, max) => {
		this.setState({ min, max });
	};

	addOnScroll = (cb) => {
		this.setState((st) => ({
			onScrollCb: [ ...st.onScrollCb, cb ]
		}));
	};

	removeOnScroll = (callback) => {
		this.setState((st) => ({
			onScrollCb: st.onScrollCb.filter((cb) => cb !== callback)
		}));
	};

	setScroll(delta, force = false) {
		this.setState(
			(st) => {
				if (st.retainCeroScroll) return;

				let scroll = st.scroll + delta;
				if (force) scroll = delta;

				let retainCeroScroll =
					(scroll === 0 || scroll <= st.min || scroll >= st.max) && scroll !== st.scroll;

				if (retainCeroScroll) {
					setTimeout(() => this.setState({ retainCeroScroll: false }), 1000);
				}
				scroll = ~~Math.min(Math.max(scroll, st.min), st.max);
				return { scroll, retainCeroScroll, lastScroll: st.scroll };
			},
			() => {
				this.state.onScrollCb.forEach((cb) => cb(this.state.scroll, this.state.lastScroll));
			}
		);
	}

	handleScroll(e) {
		if (e.deltaY < 0) {
			this.scrollUp();
		} else if (e.deltaY > 0) {
			this.scrollDown();
		}
	}

	render() {
		return (
			<AppContext.Provider
				value={{
					...this.state,
					scrollUp: this.scrollUp,
					scrollDown: this.scrollDown,
					scrollAll: this.scrollAll,
					resetScroll: this.resetScroll,
					setMinMaxScroll: this.setMinMaxScroll,
					addOnScroll: this.addOnScroll,
					removeOnScroll: this.removeOnScroll
				}}
			>
				{this.props.children}
			</AppContext.Provider>
		);
	}
}
