import React, { Component } from 'react';
import Transition from '../components/Transition';
import Console from '../components/Console';
import { AppContext } from '../contexts/AppContext';

export default class About extends Component {
	static contextType = AppContext;
	static defaultProps = {
		lines: [
			'Hello! My name is David,',
			'fullstack developer jr.',
			"I'm looking for an oportunity to colaborate with you.",
			'print <$my-photo>',
			'print <$my-description>',
			'',
			'Those are my main skills:',
			'print <Javascript> <4>',
			'print <React> <4>',
			'print <Redux> <4>',
			'print <Sass> <4>',
			'print <Html> <4>',
			'print <Css> <4>',
			'print <Mongo db> <2.5>',
			'print <Express> <2.5>'
		],
		skills: [ 'javascript', 'react', 'redux', 'sass', 'html', 'css', 'express', 'mongo db' ],
		description:
			' "I\'m currently developing my skills as Fullstack web developer. Before i came to this place i was bent on accumulating hands-on software development projects using multiple programming languages. I will be a life-long student, always pursuing higher standard while remain curious and passionate about everything in the web world."'
	};

	constructor(props, context) {
		super(props, context);
		this.state = {
			skills: [],
			showDescription: false,
			showPhoto: false
		};
		this.context.setMinMaxScroll(-100, 100);
		this.context.resetScroll();
	}

	renderDots(level) {
		let dots = [];
		for (let i = 0; i < 5; i++, level--) {
			if (level >= 1) {
				dots.push(<div key={i} className={'About__dot fill'} />);
			} else if (level > 0) {
				dots.push(<div key={i} className={'About__dot half'} />);
			} else {
				dots.push(<div key={i} className={'About__dot empty'} />);
			}
		}
		return dots;
	}

	renderSkills = () => {
		return this.state.skills.map(({ skill, level }, i) => (
			<div key={`About-${i}`} className="About__skill">
				{skill}
				<div className="About__dots">{this.renderDots(level)}</div>
			</div>
		));
	};

	renderDescription = () => {
		const { showDescription } = this.state;
		return showDescription ? (
			<div className="About__description">{this.props.description}</div>
		) : (
			''
		);
	};

	renderPhoto = (domain) => {
		const { showPhoto } = this.state;
		return showPhoto ? (
			<img className="About__photo" src="https://i.ibb.co/5nDmHvg/me.jpg" alt="selfie" />
		) : (
			''
		);
	};

	handleEndLine = (line) => {
		if (!line) return;

		if (line.toLowerCase().includes('my-description')) {
			this.setState({ showDescription: true });
		} else if (line.toLowerCase().includes('my-photo')) {
			this.setState({ showPhoto: true });
		} else {
			let skill, level;

			this.props.skills.forEach((sk) => {
				if (line.toLowerCase().includes(sk)) {
					level = +line.replace(new RegExp(sk, 'gi'), '').replace(/[ <>]|print/g, '');
					skill = sk.toUpperCase();
					this.setState((st) => ({
						skills: [ ...st.skills, { skill, level } ]
					}));
				}
			});
		}
	};

	render() {
		const { scroll, scrollAll, domain } = this.context;
		const { lines } = this.props;
		return (
			<React.Fragment>
				<div className="About" style={{ transform: `translateX(${-scroll}%)` }}>
					<Console lines={lines} onEndLine={this.handleEndLine} />
					<div className="About__skills">{this.renderSkills()}</div>
					<div className="About__container">
						{this.renderDescription()}
						{this.renderPhoto(domain)}
					</div>
					<div className="About__arrow left" onClick={() => scrollAll(-100)}>
						<i className="fas fa-arrow-left" />
					</div>
					<div className="About__arrow right" onClick={() => scrollAll()}>
						<i className="fas fa-arrow-right" />
					</div>
				</div>
				<Transition isWhite fromRight to={`${domain}/portfolio`} text="Portfolio" />
				<Transition isWhite fromLeft to={`${domain}/`} text="Home" />
			</React.Fragment>
		);
	}
}
