import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { AppContext } from '../contexts/AppContext';

class Transition extends Component {
	static contextType = AppContext;
	static defaultProps = {
		delay: 2000
	};
	constructor(props, context) {
		super(props, context);
		this.state = {
			isDone: false,
			calcScroll: this.props.fromLeft ? -100 : 100
		};
	}

	componentDidMount() {
		this.context.addOnScroll(this.handleScroll);
	}

	componentWillUnmount() {
		this.context.removeOnScroll(this.handleScroll);
	}

	handleScroll = (scroll, last) => {
		// console.log('Transition scroll event', scroll, last);
		const { fromLeft, to, history, delay } = this.props;
		let calcScroll;

		if (scroll <= 0 && fromLeft) {
			calcScroll = -scroll - 100;
		} else if (scroll >= 0 && !fromLeft) {
			calcScroll = 100 - scroll;
		} else if (fromLeft) {
			calcScroll = -100;
		} else {
			calcScroll = 100;
		}

		if (!this.state.isDone && last !== scroll && calcScroll === 0) {
			console.log('Done', to);
			if (to) setTimeout(() => history.push(to), delay);
		}

		this.setState({ calcScroll, isDone: calcScroll === 0 });
	};

	render() {
		const { text, fromLeft, isWhite } = this.props;
		const { calcScroll } = this.state;

		return (
			<div
				className={`Transition ${isWhite ? 'white' : ''} ${fromLeft ? 'left' : ''}`}
				style={{ transform: `translateX(${calcScroll}%)` }}
			>
				<h1 className="Transition__text"> {text}</h1>
				<div
					className="Transition__bar"
					style={{ transform: `translateX(${-2 * calcScroll}%)` }}
				/>
			</div>
		);
	}
}

export default withRouter(Transition);
