import React, { Component } from 'react';
import Transition from '../components/Transition';
import { AppContext } from '../contexts/AppContext';

export default class Demo extends Component {
	static contextType = AppContext;
	constructor(props, context) {
		super(props, context);
		this.context.setMinMaxScroll(-100, 0);
		this.context.resetScroll();
	}
	render() {
		const { scroll, scrollAll, domain } = this.context;
		const { match } = this.props;

		let url = '';
		switch (match.params.demo) {
			case 'carddealer':
				url = 'https://david1906.github.io/cardDealer/';
				break;
			case 'moviefight':
				url = 'https://david1906.github.io/movieFight/';
				break;
			case 'maze':
				url = 'https://david1906.github.io/mazeTest/';
				break;
			case 'trenligero':
				url = 'https://david1906.github.io/trenLigero/';
				break;
			case 'dadjokes':
				url = 'https://david1906.github.io/dadJokes/';
				break;
			case 'lightsout':
				url = 'https://david1906.github.io/lightsOut/';
				break;
			case 'todoapp':
				url = 'https://david1906.github.io/todoApp/';
				break;
			case 'colorsapp':
				url = 'https://david1906.github.io/colorsApp/';
				break;
			default:
		}
		return (
			<React.Fragment>
				<div className="Demo" style={{ transform: `translateX(${-scroll}%)` }}>
					<a href={url} className="Demo__title">
						{match.params.demo}
					</a>
					<div className="Demo__app">
						<iframe src={url} frameBorder="0" />
					</div>
					<div className="Demo__arrow" onClick={() => scrollAll(-100)}>
						<i className="fas fa-arrow-left" />
					</div>
				</div>;
				<Transition fromLeft to={`${domain}/portfolio`} text="Portfolio" />
			</React.Fragment>
		);
	}
}
