import React, { Component } from 'react';
import Card from '../components/Card';
import Transition from '../components/Transition';
import { AppContext } from '../contexts/AppContext';

export default class Portfolio extends Component {
	static contextType = AppContext;
	static defaultProps = {
		cards: [
			{ title: 'colors app', imgUrl: 'https://i.ibb.co/TqN54zD/colors-App.png' },
			{ title: 'movie fight', imgUrl: 'https://i.ibb.co/QM2pxyZ/moviefight.png' },
			{ title: 'card dealer', imgUrl: 'https://i.ibb.co/jvN43Q1/card-Dealer.png' },
			{ title: 'maze', imgUrl: 'https://i.ibb.co/5GNHjb6/mazeTest.png' },
			{ title: 'dad jokes', imgUrl: 'https://i.ibb.co/z2NmvrR/dadJokes.png' },
			{ title: 'lights out', imgUrl: 'https://i.ibb.co/j6tmnBV/lights-Out.png' },
			{ title: 'tren ligero', imgUrl: 'https://i.ibb.co/QYt9SXt/tren-Ligero.png' },
			{ title: 'todo app', imgUrl: 'https://i.ibb.co/LnyQ4Kp/todoApp.png' }
		]
	};

	constructor(props, context) {
		super(props, context);
		this.context.setMinMaxScroll(-100, 0);
		this.context.resetScroll();
	}

	renderCards = (domain) => {
		return this.props.cards.map(({ title, imgUrl }, i) => {
			return (
				<Card
					title={title}
					imgUrl={`${imgUrl}`}
					key={i}
					to={`${domain}/portfolio/${title.replace(' ', '')}`}
				/>
			);
		});
	};
	render() {
		const { scroll, scrollAll, domain } = this.context;
		return (
			<React.Fragment>
				<div className="Portfolio" style={{ transform: `translateX(${-scroll}%)` }}>
					{this.renderCards(domain)}
					<div className="Portfolio__arrow" onClick={() => scrollAll(-100)}>
						<i className="fas fa-arrow-left" />
					</div>
				</div>
				<Transition fromLeft to={`${domain}/about`} text="About Me" />
			</React.Fragment>
		);
	}
}
