import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Card extends Component {
	render() {
		const {
			title,
			to,
			imgUrl = 'https://images.unsplash.com/photo-1525543907410-b2562b6796d6?ixlib=rb-0.3.5&s=9ff8e5e718a6a40cbd0e1471235912f4&auto=format&fit=crop&w=3452&q=80'
		} = this.props;
		return (
			<Link className="Card" to={to}>
				<img src={imgUrl} alt="" className="Card__img" />
				<div className="Card__side">
					<h2 className="Card__title">{title}</h2>
					<i className="Card__link fas fa-arrow-right" />
				</div>
			</Link>
		);
	}
}
